import { UserLogService } from 'src/app/service/userLog.service';
import { Component, OnInit, Input } from '@angular/core';
import { UserService } from 'src/app/service/user.service';
import { Router, ActivatedRoute } from '@angular/router';
import { RoleService } from 'src/app/service/role.service';

@Component({
  selector: 'app-user-details',
  templateUrl: './user-details.component.html',
  styleUrls: ['./user-details.component.scss'],
  providers: [UserService, UserLogService]
})
export class UserDetailsComponent implements OnInit {
  uid: number;
  constructor(private userService: UserService,private userLogService: UserLogService, private _roleService: RoleService, private router: ActivatedRoute) {
  }
  userDetails;
  roles;
  newUser;
  userLog;
  ngOnInit() {
    this.router.params.subscribe(params => {
      this.uid = +params['id'];
    });
    this.GetUserdata(this.uid);
    this._roleService.GetAllRoles().subscribe(data => this.roles = data);
  }
  GetUserdata(id) {
    this.userService.GetUserDetails(id).subscribe(x => {
      this.userDetails = x[0];
      this.userLog = x;
    });
  }
  findIdByNameRole(roleNameSelected): number {
    var t = -1;
    this.roles.forEach(e => {
      if (e.RoleName == roleNameSelected) {
        t = e.RoleId;
      }
    });
    return t;
  }
  onSubmit(formEdit, uid) {
    this.newUser = formEdit.value;
    this.newUser.RoleId = this.findIdByNameRole(this.newUser.RoleId);
    this.newUser.Gender = this.newUser.Gender === "Male" ? true : false;
    this.newUser.UserId = uid;
    this.UpdateUsers(uid);
  }
  private UpdateUsers(uid) {
    this.userService.UpdateUser(this.newUser).subscribe
      (success => this.GetUserdata(uid));
  }
}
