import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class RoleService {

  constructor(public http:HttpClient) { }
  url: string = "http://localhost:19949/api/roles";

  public GetAllRoles(){
    var reqHeader = new HttpHeaders({ 'Content-Type': 'application/x-www-urlencoded','No-Auth':'False' });
    return this.http.get(this.url);
  }
  public AddRole(role){
    var reqHeader = new HttpHeaders({ 'Content-Type': 'application/x-www-urlencoded','No-Auth':'False' });
    return this.http.post(this.url,role);
  }
  public DeleteRole(id){
    var reqHeader = new HttpHeaders({ 'Content-Type': 'application/x-www-urlencoded','No-Auth':'False' });
    return this.http.delete(this.url,id);
  }
  public UpdateRole(role){
    var reqHeader = new HttpHeaders({ 'Content-Type': 'application/x-www-urlencoded','No-Auth':'False' });
    return this.http.put(this.url,role);
  }
  public GetRoleDetails(id){
    var reqHeader = new HttpHeaders({ 'Content-Type': 'application/x-www-urlencoded','No-Auth':'False' });
    const url="http://localhost:19949/api/roles?uid="
    return this.http.get(url+id);
  }
}
